body{
  background-color:#F2F6FF;
  margin:0;
  font-family: 'Raleway', sans-serif;
}
.top-bar{
  padding:0px 30px 0px 30px;
  background-color:#fff;
  box-shadow:0px 0.5px 0.2px lightgrey;
  position: fixed;
  top:0;
  left:0;
  right:0;
  z-index: 50;
}
.logo{
  background-image: -webkit-linear-gradient(181deg,#0048ff,#0096ff),-webkit-linear-gradient(#000,#000);
  color: #0084ff;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 32px;
  font-weight:900;
  margin-top: 0;
  margin-left: 50px;
  width: 130px;
  -webkit-font-smoothing: antialiased;
  font-style: normal;
  font-stretch: normal;
  line-height: 0;
  letter-spacing: -.9px;
  text-align: left;
}
.dropdown-text{
  font-weight:500;
  margin-left:20px;
  margin-top: 18px;
  font-size: 15px;
  opacity: 0.7;
}
.dropdown-text-icon{
  display:inline-block;
  padding-top:0px;
  padding-left:8px;
  font-size:small;
}
.logo-stories-text{
  font-style:italic;
  font-weight:500;
  position: relative;
  top: 15px;
  right: 138px;
}
.top-bar-msg{
  max-width:311px;
  opacity: .6;
  font-size: 15px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: 1;
  letter-spacing: .4px;
  text-align: left;
  color: rgba(0,0,0,.6);
  margin-left: 279px;
  margin-top: 7px;
}
.top-bar-btn{
  width: auto;
  font-size: 0.75em;
  padding: 0.5em 2em;
  margin-left: 38px;
  margin-bottom:10px;
  box-shadow: none;
  min-width: 136px;
  height: 42px;
  border-radius: 25px;
  word-wrap: break-word;
  box-sizing: border-box;
  border: none;
  cursor: pointer;
  color: #fff;
  outline: none;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: .03em;
  background: linear-gradient(269deg,#0048ff,#0096ff);
}
.Category-section{
  margin-left:230px;
  padding-top:120px;
  max-width: 900px;
}
.Category-section-heading{
  margin:0;
  font-size: 20px;
}
.filter-text{
  font-size:12px;
  font-weight:600;
  margin-top:25px;
}
.filter-icon{
  padding-right:8px;
  color:#0084ff;
  font-size:16px
}
.categories-list{
  margin-top:25px;
}
.categories{
  padding:6px 10px;
  color:#0084ff;
  border:1px solid #0084ff;
  border-radius:15px;
  font-size:13px;
  font-weight:600;
  display:inline-block;
  margin:5px;
  cursor: pointer;
}
.filter-select{
  background-color:#0084ff;
  color:#fff;
}
#blog-section{
  margin-left:210px;
  padding-top:30px;
  max-width: 900px;
  display: flex;
  flex-wrap: wrap;
}
.card{
  display: flex;
  margin-left: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
  border:1px solid rgba(0,168,255,.3);
  max-height: 25em;
}
.card-img-top{
  border-top-left-radius: 9px;
  border-top-right-radius: 9px;
}
.card-body{
  padding: 1rem 1.5rem;
}
.blog-title{
  font-size:18px;
  text-align:left;
  margin:0;
  line-height: 1.3;
  font-weight:600;
  width: 200px;
  height: 50px;
  overflow: hidden;
}
.blog-author{
  text-decoration:none;
  color:#0096ff;
  font-size:14px;
  margin-top: 3px;
  font-weight:500;
}
.blog-date{
  color:rgba(0,0,0,0.7);
  font-size:14px;
  padding-left:5px;
  font-family: 'Poppins', sans-serif;
  font-weight:600;
}
.blog-content{
  color:rgba(0,0,0,.3);
  font-size:14px;
  font-weight:400;
  line-height:1.5;
  margin-top: 16px;
  max-height: 109px;
  overflow: hidden;
}
